import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/MusicPlatformer/image1.png"
import image2 from "../../static/MusicPlatformer/image2.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>Music Platformer</h1>
        
        <img src={image1} alt="image1"/>
        <img src={image2} alt="image2"/>

        <div>
          <p>
          This is a very early version of what I hope to be a game one day. It is a fairly simple music platformer where every ability has a music note attached to it. The player's goal is to use the abilities in the correct order to play the song which is attached to the current level.
          I have also used this project to play a bit with Unity shaders and create a few visual effects. (The water in the image is not mine, it's from a Unity store package I've bought, only the ball smear and squish are mine.)

          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage